
.cookie-bar {
    position: sticky;
    width: 100%;
    bottom: 0;
    // right: 0;
    // left: 0;
    height: 30px;
    text-align: center;
    line-height: 30px;
    background: linear-gradient(lighten($headerdarkColor, 10%), darken($headerdarkColor, 10%));
    color: white;
    font-size: 12px;
    font-family: $secondaryFont;
    font-weight: 100;
    transition: .8s;
    animation: slideIn .8s;
    animation-delay: .8s;
    .message {
      white-space: nowrap;
      text-shadow: 0 1px 0 darken($headerdarkColor, 10%);
      @media (max-width: 767px){
        display: none;
      }
    }
    .mobile {
      display: none;
      @media (max-width: 767px){
        display: inline-block;
      }
    }
  }
  @keyframes slideIn {
    0% {
      transform: translateY(-50px);
    }
    100% {
      transform: translateY(0);
    }
  }
  .close-cb {
    border: none;
    color: white;
    background: darken($headerdarkColor, 20%);
    position: absolute;
    display: inline-block;
    right: 10px;
    top: 0;
    cursor: pointer;
    border-radius: 3px;
    box-shadow: inset 0 0 3px 0 rgba(0,0,0,.2);
    line-height: 30px;
    height: 30px;
    width: 30px;
    font-size: 16px;
    font-weight: bold;
    &:hover {
      background: darken($headerdarkColor, 10%);
    }
  }
  .checkbox-cb {
    display: none;
    &:checked + .cookie-bar {
      transform: translateY(-50px);
      display: none;
    }
  }
  a {
    color: rgb(92, 242, 255);;
  }
